.l-container {
  max-width: 100%;
}

@media only screen and (max-width: 400px) {
  .l-container {
    width: 100%;
  }
}

@media only screen and (min-width: 1200px) {
  .l-container {
    padding-left: calc(235px + 80px);
  }
}

.p-header {
  background: url("./../img/bg_about@2x.png") no-repeat center/cover;
  height: 100vh;
  position: relative;
}

.p-header .slide__controller {
  height: 40px;
  position: absolute;
  bottom: 60px;
  right: 60px;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.p-header .slide__controller .slide__num {
  font-size: 3.2rem;
  color: white;
  height: 100%;
}

.p-header .slide__controller .slide__num span {
  transform: translate3d(0, -100%, 0);
}

.p-header .slide__controller .slide__num .is-current {
  display: block;
  transform: translate3d(0, 100%, 0);
}

.p-header .slide__controller .slide__border {
  position: relative;
  height: 3px;
  width: 70px;
  background-color: #777;
  overflow: hidden;
}

.p-header .slide__controller .slide__border span {
  position: absolute;
  left: 0;
  top: 0;
  background-color: white;
  height: 100%;
  width: 0;
  display: block;
  padding: unset;
}

.p-header .slide__controller .slick-arrow {
  width: 15px;
  cursor: pointer;
}

.p-header .slide__controller span {
  padding: 0 20px;
  color: white;
  font-size: 3.2rem;
}

.p-header .slide__controller.is-move .slide__border span {
  width: 100% !important;
  left: 100%;
  transition: left 700ms;
}

.p-header__slide {
  height: 100%;
  position: relative;
}

.p-header__slide .slick-arrow {
  position: absolute;
  bottom: 30px;
  background-color: transparent;
  outline: none;
  width: 15px;
  z-index: 999;
  cursor: pointer;
}

.p-header__slide .slick-next {
  right: 30px;
}

.p-header__slide .slick-prev {
  right: 130px;
}

.p-header__slide .slick-list {
  height: 100%;
}

.p-header__slide .slick-list .slick-track {
  height: 100%;
}

.p-header .c-nav {
  height: 100%;
}

.p-header .is-white {
  color: #fff;
}

.p-header .is-white .c-nav__logo--white {
  display: block;
}

.p-header .is-white .c-nav__logo--black {
  display: none;
}

.p-header__menu {
  position: absolute;
  top: 59px;
  right: 60px;
  width: 46px;
}

@keyframes count {
  0% {
  }
  100% {
    transform: translateY(-100%);
  }
}

.p-feature {
  min-height: 100vh;
  background-color: #e8e8e8;
  padding-top: 97px;
  padding-bottom: 50px;
}

.p-feature__slide {
  margin-top: 27px;
}

.p-feature__slide .slide__item:not(:last-of-type) {
  margin-right: 30px;
}

.p-feature__slide .slide__item .slide__img {
  width: 840px;
  height: 560px;
}

.p-feature__slide .slide__item .slide__title {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 3.6rem;
  letter-spacing: 1.1px;
}

.p-feature__slide .slide__item .slide__link {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 2.3rem;
  position: relative;
}

.p-feature__slide .slide__item .slide__link:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #000;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.p-feature__slide .slide__item .slide__bottom {
  margin-top: 27px;
}

.p-feature__slide .slick-arrow {
  background-color: transparent;
  outline: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  width: 20px;
  cursor: pointer;
}

.p-feature__slide .slick-prev {
  left: 10px;
}

.p-feature__slide .slick-next {
  right: 10px;
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: black;
  z-index: 999;
  overflow: hidden;
  opacity: 0;
}

.loading__line {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  transition: width 0.4s ease-in;
}

.loading__line::before {
  content: "";
  height: 2px;
  background-color: white;
  display: block;
  width: 0;
  transition: width 0.4s ease-in;
}

.loading.is-show {
  opacity: 1;
}

.loading.is-show .loading__line::before {
  width: 100%;
}

.p-product {
  padding: 97px 0 50px;
  background-color: #d9d9d9;
}

.p-product__content {
  display: flex;
}

