.c-btn {
  border: 1px solid #111;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
}

.c-btn--primary {
  background-color: #000000;
  border-color: #111;
  color: #fff;
}

.c-nav {
  height: 100vh;
  padding: 50px 0 45px;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 235px;
  padding-left: 40px;
  justify-content: space-between;
  border-right: 1px solid #fff;
}

.c-nav li {
  display: block;
}

.c-nav li:not(:last-of-type) {
  margin-bottom: 10px;
}

.c-nav__logo {
  width: 140px;
  height: auto;
  max-height: 100%;
  margin-left: 5px;
}

.c-nav__logo--white {
  display: none;
}

.c-nav__navigation a {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 1.8rem;
  letter-spacing: 0.7px;
  padding: 0 5px;
}

.c-nav__navigation a:hover {
  background-color: #707070;
  color: white;
}

.c-nav__actions a {
  font-size: 1.8rem;
  letter-spacing: 0.7px;
  padding: 0 5px;
}

.c-nav__actions a:hover {
  background-color: #707070;
  color: white;
}

.c-slide {
  position: relative;
  height: 100%;
}

.c-slide__title {
  position: absolute;
  top: 50%;
  right: 60px;
  transform: translateY(-50%);
  color: white;
  text-align: right;
}

.c-slide__title p {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 2.4rem;
  letter-spacing: 1px;
}

.c-slide__title h3 {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 3.6rem;
  padding: 18px 0px;
  letter-spacing: 2px;
}

.c-slide__title span {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 2.2rem;
  letter-spacing: 1px;
}

.c-slide__img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.c-slide__img:hover {
  opacity: 1;
}

.c-title {
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 4.8rem;
  letter-spacing: 1px;
}

.c-product__item {
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
}

.c-product__item .c-product__text {
  text-align: center;
  font-family: neue-haas-grotesk-display, sans-serif, sans-serif;
  font-weight: normal;
  font-size: 2rem;
}

